
@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Book.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Heavy.ttf);
  font-weight: 800;
}

body * {
  font-family: "Avenir", sans-serif;
  padding: 0;
  margin: 0;
  font-weight: 300;
}

.search-form {
  margin-top: 20px;
}

.search-input {
  box-sizing: border-box;
  height: 70px;
  width: 100%;
  padding-left: 62px;
  background: 25px no-repeat url(images/search.svg);
  border-radius: 4px;
  border: 1px solid #e1e7ed;
  color: #3d4e61;
  font-size: 16px;
  font-weight: 900;
}

.search-input:hover {
  box-shadow: 0 1px 8px 0 rgba(61, 78, 97, 0.1);
}

.search-input:focus {
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  outline: none;
}

.search-input[type="text"]::placeholder {
  color: #3d4e61;
  font-size: 16px;
  font-weight: 400;
}

.search-input.small {
  padding-left: 32px;
  background: 13px center no-repeat url(images/search.svg);
  background-size: 11px;
  height: 42px;
  color: #3d4e61;
  font-size: 14px;
  font-weight: 900;
}

.search-input.small[type="text"]::placeholder {
  font-size: 14px;
}
